$desktop: 1239px;
$tabletHigh: 991px;
$tablet: 767px;
$mobile: 766px;

@mixin desktop {
    @media (max-width: #{$desktop}) {
        @content;
    }
}

/* Пример вывода:
	@media (max-width: 1439px){

	}
	Можно как хочешь менять и добавлять.

	@mixin tabletHigh {
    @media (min-width: #{$tablet}) and (max-width: #{$desktop}) {
        @content;
	}

	На выходе:
	@media (min-width: 767px) and (max-width: 1439px) {

	}

}

*/

@mixin tabletHigh {
    @media (max-width: #{$tabletHigh}) {
        @content;
    }
}

@mixin tablet {
    @media (max-width: #{$tablet}) {
        @content;
    }
}

@mixin mobile {
    @media (max-width: #{$mobile}) {
        @content;
    }
}
