.main-btn {
    display: inline-block;
    vertical-align: middle;
    padding: 18px 36px;
    margin: 0;
    background-color: #fff;
    border-radius: 50px;
    border: 1.5px solid #000;
    box-sizing: border-box;
    font-family: $font-bold;
    font-size: 18px;
    line-height: 20px;
    color: #000;
    white-space: nowrap;
    text-align: center;
    transition: all .25s ease-in-out;

    &:hover {
        color: rgba(31, 34, 41, .7);
        border-color: rgba(31, 34, 41, .7);
    }

    @include desktop {

        &:hover {
            color: #000;
            border-color: #000;
        }
    }

/*    // mobile
    @include mobile {
        display: flex;
        width: 275px;
        margin: 0 auto;
    }*/
}
