.footer {
    width: 100%;
    height: 160px;
    background-color: #000;
    display: flex;
    align-items: center;

    &__container {
        margin: 0 auto;
    }

    p {
        font-family: $font-reg;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        color: #fff;
        margin: 0;
    }

    // mobile
    @include mobile {
        height: 144px;
    }
}
