body {
		background: $white;
		-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

h3 {
    font-family: $font-headlines;
    font-size: 46px;
    line-height: 52px;
    color: $dark;
    margin-bottom: 23px;
    text-align: left;
    font-weight: normal;
}

.slider-controls {
    position: relative;
    width: 104px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &.ie-controls {
        button {
            &:after {
                display: none;
            }

            &.left-swipe:hover {
                svg {
                    transform: translate(-50%, -50%);
                }
            }

            &.right-swipe:hover {
                svg {
                    transform: translate(-50%, -50%);

                }
            }
        }
    }

    button {
        display: block;
        width: 45px;
        height: 45px;
        position: relative;
        background: transparent;
        border: none;
        outline: none;

        &:after {
            content: '';
            display: block;
            width: 19px;
            height: 2px;
            background-color: $accent;
            position: absolute;
            top: 50%;
            transform: translate(0%, -50%);
            transition: all 0.4s;
            z-index: 0;
        }

        svg {
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            transition: all 0.4s;
            z-index: 3;
        }

        path {
            transition: fill 0.4s;
            fill: #D6D6D6;
        }


        &.left-swipe {
            &:after {
                right: 3px;
            }
        }

        &.right-swipe {
            &:after {
                left: 2px;
            }
        }

        &.left-swipe:hover {
            svg {
                transform: translate(-65%, -50%);
            }
        }

        &.right-swipe:hover {
            svg {
                transform: translate(-35%, -50%);
            }
        }

        &.left-swipe[aria-disabled="true"]:hover {
            svg {
                transform: translate(-50%, -50%);
            }
        }

        &.right-swipe[aria-disabled="true"]:hover {
            svg {
                transform: translate(-50%, -50%);
            }
        }

        &:hover {
            // &:after {
            //     opacity: 1;
            // }

            path {
                fill: $accent;
            }
        }

        &[aria-disabled="true"] {
            cursor: default;
        }

        &[aria-disabled="true"]:hover {
            path {
                fill: #D6D6D6;
            }
        }

        &[aria-disabled="false"] {
            path {
                fill: $accent;
            }

            // &:after {
            //     opacity: 1;
            // }
        }
    }
}

a {
    text-decoration: none;
}

.stop-scrolling {
    overflow: hidden;
    height: 100vh;
    width: 100vw;
}

.social-link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;

    &--header {
        width: 240px;
        padding-top: 88px;
        margin: 0 auto;
        margin-bottom: 20px;

        .social-link__item {

            svg {
                fill: #FF59A3;
            }
        }
    }

    &__item {
        width: 28px;
        height: 28px;
        background-color: #fff;
        border-radius: 50%;
        position: relative;
        transition: background-color .25s ease-in-out;

        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: #000;
            transition: all .25s ease-in-out;
        }

        &:hover {

            svg {
                fill: #fff;
            }
        }

        &:not(:last-of-type) {
            margin-right: 24px;
        }

        &--fb {

            svg {
                width: 8px;
                height: 16px;
            }


            &:hover {
                background-color: #3b5998;

                .svg {
                    fill: #fff;
                }
            }
        }

        &--yt {

            svg {
                width: 16px;
                height: 12px;
            }

            &:hover {
                background-color: #e42117;

                .svg {
                    fill: #fff;
                }
            }
        }

        &--vk {

            svg {
                width: 16px;
                height: 9px;
            }

            &:hover {
                background-color: #597da3;

                .svg {
                    fill: #fff;
                }
            }
        }

        &--ok {

            svg {
                width: 10px;
                height: 14px;
            }

            &:hover {
                background-color: #ee8208;

                .svg {
                    fill: #fff;
                }
            }
        }

        &--tw {

            svg {
                width: 14px;
                height: 12px;
            }

            &:hover {
                background-color: #1da1f2;

                .svg {
                    fill: #fff;
                }
            }
        }
    }
}

.hidden-block {
    opacity: 0;
    transition: all .5s ease-in-out;
    transform: translateY(80px);
    transition-delay: 0.1s;

    &--r {
        transform: translateY(0) translateX(80px);
    }

    &--l {
        transform: translateY(0) translateX(-80px);
    }
}

.animated-block {
    opacity: 1;
    transition: all .5s ease-in-out;
    transform: translateY(0px) translateX(0);
}

// tablet-portrait
@include desktop {

    .social-link {

        &--header {

            .social-link__item {

                &:hover {
                    background-color: #fff;

                    svg {
                        fill: #ff59a3;
                    }
                }
            }
        }

        &__item {

            &:hover {

                svg {
                    fill: #000;
                }
            }

            &--fb {

                &:hover {
                    background-color: #fff;

                    .svg {
                        fill: #000;
                    }
                }
            }

            &--yt {

                &:hover {
                    background-color: #fff;

                    .svg {
                        fill: #000;
                    }
                }
            }

            &--vk {

                &:hover {
                    background-color: #fff;

                    .svg {
                        fill: #000;
                    }
                }
            }

            &--ok {

                background-color: #fff;

                .svg {
                    fill: #000;
                }
            }

            &--tw {

