.about {
    padding-top: 80px;
    margin-bottom: 80px;

    &__container {
        width: 1200px;
        margin: 0 auto;
    }

    &__top {
        width: 100%;
        height: 700px;
        background: #fff url('../img/about/about-image-1.png') no-repeat center left;
        background-size: cover;
        position: relative;

        .container {
            width: 540px;
            padding-top: 214px;
            padding-bottom: 214px;
        }

        h2 {
            font-family: $font-uniq;
            font-size: 64px;
            line-height: 84px;
            text-transform: uppercase;
            color: #fff;
            margin: 0;
            margin-bottom: 20px;
            transition-delay: .1s;
            font-weight: normal;
        }

        p {
            font-family: $font-reg;
            font-size: 18px;
            line-height: 28px;
            color: #fff;
            margin: 0;
            transition-delay: .15s;
        }

        span {
            display: block;
            font-family: $font-reg;
            font-size: 36px;
            line-height: 56px;
            color: #fff;
            transition-delay: .2s;
        }
    }

    &__bottom {
        width: 100%;
        position: relative;
        background: #F9F9F9;
        overflow: hidden;

        &.animated-image {

            &:after {
                opacity: 1;
                transform: translateX(0);
            }
        }

        &:after {
            content: '';
            position: absolute;
            top: -70px;
            right: 0;
            background: url('../img/about/about-image-2.png') no-repeat bottom right;
            width: 100%;
            height: 850px;
            z-index: 1;
            opacity: 0;
            transform: translateX(80px);
            transition: all .5s .3s ease-in-out;
        }

        .container {
            width: 490px;
            padding-top: 126px;
            padding-bottom: 126px;
            position: relative;
            z-index: 2;
        }

        h3 {
            font-family: $font-headlines;
            font-size: 46px;
            line-height: 52px;
            color: #000;
            margin: 0;
            margin-bottom: 8px;
            font-weight: normal;
        }

        p {
            font-family: $font-reg;
            font-size: 16px;
            line-height: 29px;
            color: #000;
            margin: 0;
            margin-bottom: 12px;

            &:first-of-type {
                font-family: $font-headlines-reg;
                font-weight: 400;
                font-size: 26px;
                margin-bottom: 40px;
            }

            &:last-of-type {
                margin-bottom: 55px;
            }
        }
    }

    &__age-limit {
        width: 48px;
        height: 48px;
        background-color: #000;
        position: absolute;
        right: 120px;
        bottom: 20px;

        span {
            font-family: $font-reg;
            font-size: 16px;
            line-height: 25px;
            color: #fff;
            padding-top: 12px;
            text-align: center;
        }
    }

    // tablet-horizontal
    @include desktop {
        width: 100%;

        &__container {
            width: 90%;
            max-width: 1080px;
        }

        &__top {
            background-position-x: -110px;
        }

        &__bottom {
            overflow: hidden;

            .container {
                padding-top: 80px;
                padding-bottom: 80px;
            }

            &:after {
                top: -40px;
                right: -170px;
                height: 630px;
                background-position: top right;
                background-size: 68%;
            }
        }
    }

    // tablet-portrait
    @include tabletHigh {

        &__top {
            height: 944px;
            background: #fff url('../img/about/about-tablet-image-1.png') no-repeat top left;
            background-position-y: 0;
            background-size: cover;

            .container {
                padding-top: 80px;
            }
        }

        &__bottom {
            overflow: hidden;

            &:after {
                top: 110px;
                right: -100px;
                width: 72%;
                height: 537px;
                background-size: 90%;
            }

            .container {
                width: 380px;
                padding-top: 80px;
                padding-bottom: 80px;
            }

            h3 {
                font-size: 40px;
                line-height: 45px;
                margin-bottom: 10px;
            }

            p {

                &:last-of-type {
                    margin-bottom: 35px;
                }
            }
        }
    }

    // mobile
    @include mobile {
        padding-top: 56px;

        &__container {
            width: 85%;
            max-width: initial;
        }

        &__top {
            height: calc(100vh - 56px);
            background-position: top center;

            .container {
                width: 100%;
                padding-top: 40px;
                padding-bottom: 40px;
            }

            h2 {
                font-size: 32px;
                line-height: 42px;
                margin-bottom: 10px;
            }

            p {
                font-size: 14px;
                line-height: 22px;
            }

            span {
                font-size: 18px;
                line-height: 26px;
            }
        }

        &__bottom {
            padding-top: 80px;

            &:after {
                width: 100%;
                height: 320px;
                background-size: contain;
                top: auto;
                bottom: 0;
                right: 0;
            }

            .container {
                width: 100%;
                padding-top: 0;
                padding-bottom: 312px;
            }

            h3 {
                font-size: 32px;
                line-height: 36px;
                margin-bottom: 8px;
            }

            p {

                &:first-of-type {
                    font-size: 18px;
                    line-height: 28px;
                }

                &:last-of-type {
                    margin-bottom: 45px;
                }
            }
        }

        &__age-limit {
            right: 20px;
        }
    }
}
