.films {
    background: rgba(0, 0, 0, 0.9);
    padding: 67px 0 104px;
    margin-bottom: 128px;

    h3 {
        color: $white;
        margin-bottom: 39px;
    }

    .container {
        max-width: 1200px;
        width: 100%;
        margin: 0 auto;

        &:last-of-type {
            max-width: 1340px;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-wrap: wrap;
            padding-left: 70px;
            overflow: hidden;
        }
    }

    &__view {
        max-width: 452px;
        width: 100%;
        margin-right: 56px;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-wrap: wrap;
        margin-top: 10px;

        &--image {
            max-width: 272px;
            width: 100%;
            margin-right: 33px;
            position: relative;

            &:before {
                content: '';
                display: block;
                width: 100%;
                height: 100%;
                background-color: $accent;
                position: absolute;
                z-index: 2;
                left: -8px;
                bottom: -8px;
            }

            img {
                display: block;
                width: 100%;
                height: 398px;
                position: relative;
                z-index: 7;
                transform: translate(0, -8px);
            }

            .js-mask {
                display: block;
                width: 0%;
                height: 100%;
                background-color: $accent;
                position: absolute;
                left: 0;
                top: -8px;
                z-index: 10;
            }
        }

        &--description {
            width: calc(100% - 305px);

            h4 {
                font-family: $font-headlines;
                font-size: 20px;
                line-height: 24px;
                color: $white;
                margin-bottom: 7px;
                transition: all .3s linear;
                opacity: 1;
                font-weight: normal;
                transform: translateY(0px);

                &.fade-out {
                    opacity: 0;
                    transform: translateY(20px);
                }
            }

            span {
                display: block;
            }
        }

        &--year {
            font-family: $font-reg;
            font-size: 14px;
            line-height: 22px;
            color: $white;
            margin-bottom: 40px;
            transition: all .3s linear;
            opacity: 1;
            transform: translateY(0px);

            &.fade-out {
                opacity: 0;
                transform: translateY(20px);
            }
        }

        &--date {
            font-family: $font-reg;
            font-size: 14px;
            line-height: 18px;
            color: #FF59A3;
            margin-bottom: 4px;
            transition: all .3s linear;
            opacity: 1;
            transform: translateY(0px);

            &.fade-out {
                opacity: 0;
                transform: translateY(20px);
            }
        }

        &--place {
            font-family: $font-reg;
            font-size: 14px;
            line-height: 22px;
            color: $white;
            transition: all .3s linear;
            opacity: 1;
            transform: translateY(0px);

            &.fade-out {
                opacity: 0;
                transform: translateY(20px);
            }
        }
    }

    &__slider {
        width: calc(100% - 508px);
        position: relative;

        &:after {
            content: '';
            display: block;
            width: 182px;
            height: 100%;
            position: absolute;
            top: 0;
            right: -40px;
            background: linear-gradient(270deg, #191919 26.65%, rgba(25, 25, 25, 0) 100%);
            z-index: 9;
        }

        .slider-controls button {
            path {
                fill: $accent;
            }
        }
    }

    &__item {
        width: 183px;
        margin-right: 20px;
        position: relative;
        z-index: 6;
        margin-top: 10px;
        cursor: pointer;

        &--image {
            width: 100%;
            position: relative;
            margin-bottom: 13px;

            &:before {
                content: '';
                display: block;
                width: 100%;
                height: 100%;
                background-color: $accent;
                position: absolute;
                left: 0;
                top: 0;
                position: absolute;
                z-index: 1;
            }

            img {
                display: block;
                width: 100%;
                height: 264px;
                position: relative;
                z-index: 5;
                transition: all .4s;
            }
        }

        &--title {
            font-family: $font-headlines;
            font-size: 16px;
            line-height: 20px;
            color: $white;
            margin-bottom: 4px;
            font-weight: normal;
        }

        &--year {
            font-family: $font-reg;
            font-size: 14px;
            line-height: 22px;
            color: $white;
        }

        &__schedule {
            display: none;
        }

        &:hover &--image img {
            transform: translate(8px, -8px);
        }
    }

    // tablet-horizontal
    @include desktop {

        .container {
            width: 90%;
            max-width: 1080px;
            padding-left: 0;

            &:last-of-type {
                width: 100%;
                padding-left: 5%;
                overflow: hidden;
            }
        }

        &__item {

            &:hover {
                .films__item--image img {
                    transform: initial;
                }
            }
        }
    }

    // tablet-portrait
    @include tabletHigh {
        padding-top: 80px;
        padding-bottom: 60px;
        margin-bottom: 80px;

        .container:last-of-type {
            width: 100%;
            padding-left: 5%;
        }

        h3 {
            margin-bottom: 50px;
        }

        &__view {
            width: 438px;
            margin-right: 28px;

            &--image {
                width: 266px;
                margin-right: 24px;

                &:before {
                    bottom: 0;
                }

                img {
                    height: 388px;
                }
            }

            &--description {

                h4 {
                    line-height: 31px;
                }
            }
        }

        &__slider {
            width: calc(100% - 466px);

            .slider-controls {
                margin-top: 25px;
            }
        }
    }

    @include tablet {
        &__view {
            display: none;
        }

        &__slider {
            width: 100%;

            &:after {
                background: linear-gradient(270deg, #191919 78.65%, rgba(25, 25, 25, 0) 100%);
                width: 122px;
                right: -94px;
            }

            .slider-controls {
                display: none;
            }
        }
    }

    // mobile
    @include mobile {
        padding-top: 46px;
        padding-bottom: 60px;



        &__slider {
            width: 100%;

            &:after {
                background: linear-gradient(270deg, #191919 78.65%, rgba(25, 25, 25, 0) 100%);
                width: 122px;
                right: -94px;
            }

            .slider-controls {
                display: none;
            }
        }

        &__item {

            &--year {
                margin-bottom: 15px;
            }

            &__schedule {
                display: block;
                width: 100%;

                &:first-of-type {
                    margin-top: 15px;
                }

                &:not(:last-of-type) {
                    margin-bottom: 15px;
                }

                span {
                    font-family: $font-reg;

                    &.date {
                        display: block;
                        font-size: 14px;
                        line-height: 22px;
                        color: #FF59A3;
                    }

                    &.place {
                        font-size: 12px;
                        line-height: 19px;
                        color: #fff;
                    }
                }
            }
        }
    }
}
