$icon_font_name:"svg-icons";

@font-face {
    font-family: $icon_font_name;
    src:
        url('../fonts/'+ $icon_font_name + '.woff') format('woff'),
        url('../fonts/'+ $icon_font_name + '.ttf') format('truetype');
}

.icon-facebook,
    .icon-odnoklass,
    .icon-twitter,
    .icon-vk,
    .icon-youtube,
    %last_selector {
    &:before {
        font-family: $icon_font_name;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        font-style: normal;
        font-variant: normal;
        font-weight: normal;
        // speak: none; // only necessary if not using the private unicode range (firstGlyph option)
        text-decoration: none;
        text-transform: none;
    }
}

%icons_before_after {

    &:before,
    &:after {
        font-family: $icon_font_name;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

%icons_after {
    &:after {
        font-family: $icon_font_name;
        speak: none;
        font-style: normal;
        font-weight: normal;
        font-variant: normal;
        text-transform: none;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.icon-facebook:before {
        content: '\E001';
    }

    .icon-odnoklass:before {
        content: '\E002';
    }

    .icon-twitter:before {
        content: '\E003';
    }

    .icon-vk:before {
        content: '\E004';
    }

    .icon-youtube:before {
        content: '\E005';
    }

    