.anywhere {
    background: $white;
    margin-bottom: 72px;

    .container {
        max-width: 1268px;
        width: 100%;
        padding-left: 34px;
        margin: 0 auto;
        position: relative;
        z-index: 10;

        &--map {
            padding-left: 0;
            z-index: 5;
        }

        &--slider {
            padding-right: 30px;

            @include desktop {
                padding-right: 0;
            }
        }

        &:first-of-type {
            display: flex;
            justify-content: flex-start;
            align-items: flex-end;
        }



        &:nth-child(2) {
            @include desktop {
                width: 92%;
            }
        }
    }

    &__text {
        max-width: 600px;
        width: 100%;

        p {
            font-family: $font-headlines-reg;
            font-size: 26px;
            line-height: 29px;
            color: $dark;
        }
    }

    .slider-controls {
        margin-left: calc(100% - 600px - 104px);
        bottom: -17px;
    }

    &__slider {
        position: relative;
        padding-top: 53px;

        &:after {
            content: '';
            display: none;
            width: 130px;
            height: 320px;
            position: absolute;
            top: 35px;
            right: -10px;
            background: linear-gradient(270deg, #fff 23.65%, rgba(255, 255, 255, 0) 100%);
            z-index: 10;

            @include desktop {
                display: none;
            }
        }

        &:before {
            content: '';
            display: none;
            width: 130px;
            height: 320px;
            position: absolute;
            top: 35px;
            left: -10px;
            background: linear-gradient(90deg, rgba(255, 255, 255, 1) 17%, rgba(255, 255, 255, 0) 100%);
            z-index: 10;

            @include desktop {
                display: none;
            }
        }

        &.is-after:after {
            display: none;
        }

        &.is-before:before {
            display: none;
        }

        &.no-after:after {
            display: none;
        }

        &.no-before:before {
            display: none;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-start;
        flex-shrink: 0;
        width: 183px;
        height: 188px;
        background-color: #F9F9F9;
        padding: 8px 16px 16px;
        margin-right: 20px;
        margin-bottom: 152px;
        border: 1px solid transparent;
        position: relative;
        color: $dark;
        cursor: pointer;
        transition: background-color .4s, width .25s;


        &:before {
            content: '';
            display: block;
            width: calc(100% + 2px);
            height: 340px;
            background: url('../img/anywhere/test.png');
            background-size: cover;
            background-position: top center;
            background-repeat: no-repeat;
            position: absolute;
            left: -1px;
            top: -1px;
            z-index: 5;
            opacity: 0;
            transition: opacity 0.25s;
            transition-delay: 0.1s;
        }

        &.disabled {
            background-color: transparent;
            border-color: rgba(190, 190, 190, 0.5);
        }

        &.disabled &--date,
        &.disabled &--place {
            color: #ADADAD;
        }

        &.is-active {
            width: 285px;
            z-index: 20;

            &:before {
                opacity: 1;
            }
        }

        &.is-active &--thumbnail {
            // display: block;
            // visibility: visible;
            opacity: 1;
            transition-delay: 0.3s;
        }

        &.is-active &--date,
        &.is-active &--place {
            color: white;


        }

        &:hover {
            background-color: $accent;
        }

        &:hover &--date,
        &:hover &--place {
            color: white;
        }


        &--date {
            display: block;
            width: 100%;
            font-family: $font-headlines-reg;
            font-size: 26px;
            line-height: 28px;
            color: inherit;
            transition: color .4s;
            position: relative;
            z-index: 10;

            span {
                display: block;

                font-size: 42px;
                line-height: 46px;
                margin-bottom: 10px;
            }

        }

        &--place {
            display: block;
            width: 100%;
            font-family: $font-reg;
            font-size: 14px;
            line-height: 16px;
            color: inherit;
            transition: color .4s;
            position: relative;
            z-index: 10;
        }

        &--thumbnail {
            display: block;
            width: 66px;
            height: 96px;
            position: absolute;
            top: 16px;
            right: 16px;
            z-index: 10;
            // visibility: hidden;
            opacity: 0;
            transition: opacity 0.4s;

        }

        &:last-of-type {
            margin-right: 0;
        }
    }

    &__map {
        position: relative;
        margin-top: -110px;

        #map {
            width: 100%;
            height: 419px;
            margin-bottom: 32px;
        }

        p {
            font-family: $font-reg;
            font-size: 14px;
            line-height: 16px;
            color: $dark;
            text-align: right;
            padding-right: 30px;
        }
    }

    // tablet-horizontal
    @include desktop {

        .container {
            width: 90%;
            max-width: 1080px;
            padding-left: 0;

            &--map {
                width: 92%;
            }
        }
    }

    // tablet-portrait
    @include tabletHigh {
        margin-bottom: 80px;

        &__slider {
            padding-top: 60px;
        }

        &__text {
            width: 440px;

            p {
                font-size: 24px;
                line-height: 27px;
            }
        }

        &__item {

            &:before {
                height: 304px;
            }
        }

        .slider-controls {
            position: absolute;
            right: 0;
            margin: 0;
        }

        &__map {

            #map {
                height: 288px;
                margin-bottom: 24px;
            }
        }
    }

    // mobile
    @include mobile {

        &__text {
            width: 100%;

            h3 {
                font-size: 32px;
                line-height: 36px;
                margin-bottom: 10px;
            }

            p {
                font-size: 20px;
                line-height: 24px;
            }
        }

        .swiper-wrapper {
            display: flex;
            flex-direction: column;
        }

        &__item {
            width: 100%;
            height: 104px;
            padding: 16px;
            margin-right: 0;
            margin-bottom: 0px;

            &:not(:last-of-type) {
                margin-bottom: 8px;
            }

            &:before {
                display: none;
            }

            &:hover {
                background-color: #f9f9f9;

                .anywhere__item--date,
                .anywhere__item--place {
                    color: #000;
                }
            }

            &--date {
                font-family: $font-headlines;
                font-size: 20px;
                line-height: 25px;
                font-weight: normal;

                span {
                    display: initial;
                    font-family: $font-headlines;
                    font-size: 20px;
                    line-height: 25px;
                    font-weight: normal;
                }
            }

            &--place {
                max-width: 70%;
            }

            &--thumbnail {
                width: 50px;
                height: auto;
                opacity: 1;
            }

            &.is-active {
                width: 100%;
                background: #FF59A3;

                &--date,
                &--place {
                    color: white;
                }
            }

            &.disabled {
                background-color: transparent;
                border-color: rgba(190, 190, 190, 0.5);
                opacity: .5;

                &--date,
                &--place {
                    color: #ADADAD;
                }

                &--thumbnail {
                    opacity: .5;
                }
            }
        }

        &__slider {
            padding-top: 34px;

            &:after {
                display: none;
            }
        }

        .slider-controls {
            display: none;
        }

        .container {

            &--map {
                display: none;
            }
        }


    }
}


@media screen and (min-width: 1200px) and (max-width: 1340px) {

    .anywhere .container {
        max-width: 1200px;
    }
}
