.header {
    width: 100%;
    background-color: #fff;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    position: fixed;
    z-index: 100;
    transition: transform .3s ease-in-out;

    &._hide {
        transform: translateY(-100px);
    }

    &__container {
        width: 1200px;
        height: 80px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin: 0 auto;
    }

    &__logo {
        width: 102px;

        img {
            width: 100%;
            max-width: 100%;
        }
    }

    &__menu {

        ul {
            display: flex;
            justify-content: flex-start;

            li {
                position: relative;

                &:after {
                    content: '';
                    width: 100%;
                    height: 4px;
                    background-color: transparent;
                    position: absolute;
                    left: 0;
                    right: 0;
                    bottom: -27px;
                    transition: background-color .25s ease-in-out;
                }

                &:hover {

                    &:after {
                        background-color: #ff59a3;
                    }

                    a {
                        color: #ff59a3;
                    }
                }

                &:not(:last-of-type) {
                    margin-right: 40px;
                }

                a {
                    padding-top: 28px;
                    padding-bottom: 28px;
                    font-family: $font-reg;
                    font-size: 16px;
                    line-height: 25px;
                    color: #000;
                    transition: color .25s ease-in-out;
                }
            }
        }
    }

    &__mobile {
        display: none;
    }

    &__hamburger {
        display: none;
    }

    // tablet-horizontal
    @include desktop {

        &__container {
            width: 90%;
            max-width: 1080px;
        }

        &__menu {

            ul {

                li {

                    &:hover {

                        &:after {
                            background-color: transparent;
                        }

                        a {
                            color: #000;
                        }
                    }
                }
            }
        }
    }

    // tablet-portrait
    @include tabletHigh {

        &__menu {

            ul {

                li {

                    a {
                        font-size: 18px;
                        line-height: 28px;
                    }
                }
            }
        }
    }

    // mobile
    @include mobile {

        &__container {
            height: 56px;
            position: relative;
        }

        &__logo {
            width: 70px;
            z-index: 130;
        }

        &__menu {
            display: none;
            width: 100%;
            height: calc(100vh - 50px);
            position: fixed;
            top: 50px;
            left: 0;
            z-index: 120;
            background-color: #fff;
            padding: 64px 0 24px;
            box-sizing: border-box;
            margin: 0;
            overflow: scroll;

            &.show-menu {

                .header__mobile {
                    display: block;
                }
            }

            ul {
                flex-direction: column;
                align-items: center;
                padding-left: 5%;
                padding-right: 5%;
                margin-bottom: 54px;
                box-sizing: border-box;

                li {

                    &:not(:last-of-type) {
                        margin-right: 0;
                        margin-bottom: 24px;
                    }

                    a {
                        font-family: $font-bold;
                        font-size: 24px;
                        line-height: 37px;
                        padding: 0;
                    }
                }
            }
        }

        &__mobile {
            width: 100%;
            height: 175px;
            /*background-image: url('../img/header-social-bg.svg');
            background-size: cover;
            background-repeat: no-repeat;*/
            background-color: #ff59a3;
            padding-left: 5%;
            padding-right: 5%;
            box-sizing: border-box;
            position: absolute;
            bottom: 0;

            &:after {
                content: '';
                position: absolute;
                top: -1px;
                left: 0;
                float: right;
                height: 50px;
                width: 100%;
                background: #fff;
                -webkit-clip-path: polygon(100% 0,100% 100%,0 100%);
                clip-path: polygon(100% 0,100% 100%,0 100%);
                shape-outside: polygon(100% 0,100% 100%,0 100%);
                transform: rotate(180deg);
            }

            &__copy {
                text-align: center;

                p {
                    font-family: $font-reg;
                    font-size: 14px;
                    line-height: 16px;
                    color: #FFFFFF;
                    margin: 0;
                }
            }
        }

        &__hamburger {
            width: 24px;
            height: 18px;
            display: block;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%) rotate(0deg);
            transition: .5s ease-in-out;
            z-index: 150;

            &.open {

                span {
                    left: -2px;

                    &:nth-child(1) {
                        top: 18px;
                        width: 0;
                        left: 50%;
                    }

                    &:nth-child(2) {
                        width: 29px;
                        height: 2px;
                        transform: rotate(45deg);
                    }

                    &:nth-child(3) {
                        width: 29px;
                        height: 2px;
                        transform: rotate(-45deg);
                    }

                    &:nth-child(4) {
                        top: 18px;
                        width: 0;
                        left: 50%;
                    }
                }
            }

            span {
                display: block;
                position: absolute;
                height: 2px;
                width: 100%;
                background: #000;
                border-radius: 1px;
                opacity: 1;
                left: 0;
                transform: rotate(0deg);
                transition: .25s ease-in-out;

                &:nth-child(1) {
                    top: 0;
                }

                &:nth-child(2),
                &:nth-child(3) {
                    top: 8px;
                }

                &:nth-child(4) {
                    top: 16px;
                }
            }
        }
    }
}
