.meeting {
    width: 100%;
    height: 600px;
    background: #fff url('../img/meeting/meeting-image.png') no-repeat bottom left;
    background-size: contain;
    padding-top: 108px;
    background-position-x: -80px;
    transition: all .5s ease-in-out;
    transition-delay: .2s;

    &.animated-image {
        background-position-x: 0;
    }

    &__container {
        width: 1080px;
        display: flex;
        justify-content: flex-end;
        margin: 0 auto;

        .content {
            width: 450px;
        }

        h3 {
            font-family: $font-headlines;
            font-size: 46px;
            line-height: 52px;
            color: #000000;
            font-weight: normal;
            margin: 0;
            margin-bottom: 35px;
        }
    }

    // tablet-horizontal
    @include desktop {
        width: 100%;
        background-position-x: -270px;

        &.animated-image {
            background-position-x: -190px;
        }

        &__container {
            width: 90%;
            max-width: 1080px;
        }

        .content {
            width: 360px;
        }
    }

    // tablet-portrait
    @include tabletHigh {
        height: 500px;
        padding-top: 45px;
        background-position-x: -290px;

        &.animated-image {
            background-position-x: -210px;
        }

        &__container {
            width: 98%;

            h3 {
                font-size: 40px;
                line-height: 45px;
                margin-bottom: 44px;
            }

            .content {
                width: 324px;
            }
        }
    }

    // mobile
    @include mobile {
        padding-top: 0;
        background-position: bottom left;

        background-position-x: -80px;

        &.animated-image {
            background-position-x: 0;
        }

        &__container {
            width: 85%;
            max-width: initial;

            .content {
                width: 100%;
            }

            h3 {
                font-size: 32px;
                line-height: 36px;
                margin-bottom: 32px;
            }

            .main-btn {
                margin: 0;
            }
        }
    }
}

@media (max-width: 1300px) {

    .meeting {
        background-position-x: -180px;

        &.animated-image {
            background-position-x: -100px;
        }
    }
}
